import React, { useState, useEffect } from 'react';
import { NavLink } from 'react-router-dom';
import classNames from 'classnames';
import { dynamicAdapt } from '../../js/dynamicAdapt';
import LogoWhite from '../../images/Logo-white.svg';
import LogoBrown from '../../images/Logo.svg';
import './Header.scss';

export const Header: React.FC = () => {
  const [menuOpen, setMenuOpen] = useState(false);
  const [fixedMenu, setFixedMenu] = useState(false);

  const screenWidth = window.screen.width;

  window.addEventListener('scroll', () => {
    if (window.scrollY > 128) {
      setFixedMenu(true);
    } else {
      setFixedMenu(false);
    }
  });

  dynamicAdapt();

  useEffect(() => {
    dynamicAdapt();
  }, [screenWidth]);

  return (
    <div
      className={
        classNames(`Header + ${fixedMenu
          ? 'Header--fixed'
          : ''
        }`)
      }
    >
      <div className="Header__conteiner">
        <a
          href="#header"
        >
          {!fixedMenu ? (
            <img
              className="Header__logo"
              src={LogoWhite}
              alt="logo"
            />
          ) : (
            <img
              className="Header__logo"
              src={LogoBrown}
              alt="logo"
            />
          )}
        </a>

        <nav
          className={
            classNames(`Header__nav + ${menuOpen
              ? 'Header__nav--open'
              : ''
            }`)
          }
        >
          <NavLink
            to="/home"
            className={
              classNames(`Header__link + ${fixedMenu
                ? 'Header__link--fixed'
                : ''
              }`)
            }
          >
            Home
          </NavLink>
          <NavLink
            to="/explore"
            className={
              classNames(`Header__link + ${fixedMenu
                ? 'Header__link--fixed'
                : ''
              }`)
            }
          >
            Explore
          </NavLink>
          <NavLink
            to="/travel"
            className={
              classNames(`Header__link + ${fixedMenu
                ? 'Header__link--fixed'
                : ''
              }`)
            }
          >
            Travel
          </NavLink>
          <NavLink
            to="/blog"
            className={
              classNames(`Header__link + ${fixedMenu
                ? 'Header__link--fixed'
                : ''
              }`)
            }
          >
            Blog
          </NavLink>
          <NavLink
            to="/pricing"
            className={
              classNames(`Header__link + ${fixedMenu
                ? 'Header__link--fixed'
                : ''
              }`)
            }
          >
            Pricing
          </NavLink>
        </nav>

        <div className="Header__actions">
          <NavLink
            to="/login"
            className={
              classNames(`Header__link Header__link--login + ${fixedMenu
                ? 'Header__link--fixed'
                : ''
              }`)
            }
            data-da=".Header__nav--open, 510, first"
          >
            Login
          </NavLink>
          <button
            type="button"
            className="Header__signUp"
            data-da=".Header__nav--open, 770, last"
          >
            <NavLink
              to="/signUp"
              className="Header__link Header__link--sign-up"
            >
              Sign up
            </NavLink>
          </button>

          <button
            className={
              classNames(`Header__hamburger + ${menuOpen
                ? 'Header__hamburger--open'
                : ''
              }`)
            }
            type="button"
            onClick={() => setMenuOpen(!menuOpen)}
          >
            .
          </button>
        </div>
      </div>

    </div>
  );
};
