import React from 'react';
import {
  Autoplay,
  Navigation,
  Pagination,
  EffectFade,
} from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faChevronUp, faChevronDown } from '@fortawesome/free-solid-svg-icons';
import 'swiper/swiper-bundle.min.css';
import 'swiper/swiper.scss';
import 'swiper/modules/navigation/navigation.scss';
import 'swiper/modules/pagination/pagination.scss';

import './Swiper.scss';

export const SwiperBlock = () => {
  const slideImages = [
    'https://mobimg.b-cdn.net/v3/fetch/d6/d6c21a0e2f57fa69929d5358f30002a7.jpeg',
    'https://mobimg.b-cdn.net/v3/fetch/19/196f89fbb0f5978443b9de32de99becc.jpeg',
    'https://mobimg.b-cdn.net/v3/fetch/55/55fd01f4d0b0a49a0dd17adb1d2d103d.jpeg',
    'https://mobimg.b-cdn.net/v3/fetch/a2/a2517c612d33ca455b5447149eceff81.jpeg',
    'https://mobimg.b-cdn.net/v3/fetch/42/422f8b74c7439b23f1286bb4e61e0402.jpeg',
  ];

  return (
    <Swiper
      direction="vertical"
      autoplay={{
        delay: 3500,
        disableOnInteraction: false,
      }}
      effect="fade"
      navigation={{
        prevEl: '.prev',
        nextEl: '.next',
      }}
      modules={[Autoplay, Pagination, Navigation, EffectFade]}
      pagination={{ clickable: true }}
      className="Swiper"
    >
      {slideImages.map((img) => {
        return (
          <SwiperSlide key={img}>
            <div className="Swiper__img" style={{ backgroundImage: `url(${img})` }}></div>
          </SwiperSlide>
        );
      })}
      <div className="prev Swipper__control">
        <FontAwesomeIcon icon={faChevronUp} />
      </div>
      <div className="next Swipper__control">
        <FontAwesomeIcon icon={faChevronDown} />
      </div>
    </Swiper>
  );
};
